<template>
  <section id="projects" class="section section-color-gray">
    <div class="section-header">
      <h2 class="section-heading-eyebrow">Projets</h2>
      <h3 class="section-heading">Selection de projets réalisés.</h3>
    </div>
    <div class="container container-large">
      <div id="project-list" class="layout-grid intro-grid">
        <ProjectsModal
          v-for="(project, index) in projectsLoaded"
          :key="index"
          :item="project"
          :id="id">
        </ProjectsModal>
      </div>
      <div class="row">
        <div id="loader" class="col-12 text-center">
        <a class="load-more" @click="loadMoreProjects">Voir plus</a>
        </div>
      </div>
    </div>
  </section>
</template>

<script>
import ProjectsModal from './ProjectsModal'
export default {
  name: 'ProjectsList',
  props: ['id'],
  components: {
    ProjectsModal
  },
  data: () => {
    return {
      projects: [
        {
          id: 1,
          title: 'How To Take Good Pictures',
          description:
            'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.',
          img: '/img/projects/lea/lea_template.jpg',
          carousel: [
            '/img/projects/lea/lea_img_01.jpg',
            '/img/projects/lea/lea_img_02.jpg',
            '/img/projects/lea/lea_img_03.jpg',
            '/img/projects/lea/lea_img_04.jpg',
            '/img/projects/lea/lea_img_05.jpg',
            '/img/projects/lea/lea_img_06.jpg',
            '/img/projects/lea/lea_img_07.jpg'
          ],
          open: false
        },
        {
          id: 2,
          title: 'Cherries Are Delicious',
          description:
            'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.',
          img: '/img/projects/teo/teo_template.jpg',
          carousel: [
            '/img/projects/teo/teo_img_01.jpg',
            '/img/projects/teo/teo_img_02.jpg',
            '/img/projects/teo/teo_img_03.jpg',
            '/img/projects/teo/teo_img_04.jpg',
            '/img/projects/teo/teo_img_05.jpg',
            '/img/projects/teo/teo_img_06.jpg',
            '/img/projects/teo/teo_img_07.jpg',
            '/img/projects/teo/teo_img_08.jpg'
          ],
          open: false
        },
        {
          id: 3,
          title: 'Have You Seen The Movie?',
          description:
            'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.',
          img: '/img/projects/lea/lea_template.jpg',
          open: false
        },
        {
          id: 4,
          title: 'Time For Something New',
          description:
            'Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet. Lorem ipsum dolor sit amet, consetetur sadipscing elitr, sed diam nonumy eirmod tempor invidunt ut labore et dolore magna aliquyam erat, sed diam voluptua. At vero eos et accusam et justo duo dolores et ea rebum. Stet clita kasd gubergren, no sea takimata sanctus est Lorem ipsum dolor sit amet.',
          img: '/img/projects/lea/lea_template.jpg',
          open: false
        }
      ],
      projectsToShow: 2,
      totalCount: 4
    }
  },
  computed: {
    projectsLoaded () {
      return this.projects.slice(0, this.projectsToShow)
    }
  },
  methods: {
    triggerEvent (el, event) {
      el.style.transition = 'none'
      let clickEvent = new Event(event)
      el.dispatchEvent(clickEvent)
      setTimeout(() => {
        el.style.transition = 'all .5s cubic-bezier(.65,.14,.46,.9)'
      }, 500)
    },
    loadMoreProjects () {
      if (this.projectsToShow > this.projects.length) return
      this.projectsToShow = this.projectsToShow + 2

      if (this.totalCount === this.projects.length) {
        const loader = document.getElementById('loader')
        loader.parentNode.removeChild(loader)
      }
    }
  },
  mounted () {
    if (this.id && this.projects.findIndex((item) => item.id === this.id) !== -1) {
      let elem = document.getElementById(`project-${this.id}`)
      elem.scrollIntoView()
      this.triggerEvent(elem, 'click')
    }
  }
}
</script>

<style>
a.load-more {
  position: relative;
  z-index: 1001;
  display: inline-block;
  margin-top: 24px;
  padding: 8px 22px;
  font-size: 22px;
  font-weight: 700;
  opacity: 0.75;
  letter-spacing: -0.01em;
  text-decoration: none;
  border: none;
  color: #0fa7ff
}
</style>



