<template>
  <div
    class="project-card"
    :id="`project-${item.id}`"
    ref="card"
    @mousemove="move"
    @mouseleave="leave"
  >
    <div class="modal" @click="showModal($event, item.id)" :class="{ 'show': item.open }" :style="modalStyle">
      <div class="modal-dialog">
        <div class="modal-content">
          <div class="modal-header">
            <div class="modal-img" :style="{'background-image': 'url('+ require('../assets'+ item.img)+')'}">
              <button type="button" class="close" data-dismiss="modal">
                <span>&times;</span>
              </button>
            </div>
          </div>
          <div class="modal-body">
            <h1 class="modal-title">{{ item.title }}</h1>
            <div class="description">{{ item.description }}</div>
            <div class="description">{{ item.description }}</div>
          </div>
          <div class="modal-footer">
              <div class="carousel slide" data-ride="carousel">
                <div class="carousel-inner">
                  <div class="carousel-item" v-for="(image, index) in item.carousel" :key="image.id" :class="{ 'active': index === 0 }">
                    <img :src="require('../assets'+ image)">
                  </div>
                </div>
              </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Projects',
  props: [
    'item',
    'id'
  ],
  data: () => {
    return {
      modalStyle: {
        transform: `translate(0px, 0px)`,
        zIndex: `0`
      },
      modal: {
        offsetX: 0,
        offsetY: 0,
        initialWidth: 0,
        initialHeight: 0
      },
      cardRotate: true,
      debounce: null // 3d card
    }
  },
  methods: {
    showModal (el, projectId) {
      this.item.open = !this.item.open
      document.body.classList.add('overlay-layer')
      document.body.style.overflow = 'hidden'

      // Get dims & positions of selected card
      const bounds = el.target.getBoundingClientRect()
      this.modal.offsetY = bounds.top
      this.modal.offsetX = bounds.left
      this.modal.initialWidth = bounds.width
      this.modal.initialHeight = bounds.height

      // Center the modal
      const windowWidth = window.innerWidth
      const windowHeight = window.innerHeight
      const minWidth = window.innerWidth
      const windowCenterX = windowWidth - this.modal.initialWidth
      const windowCenterY = (windowHeight - (this.modal.initialHeight * 2)) / 2
      const offsetX = (windowCenterX - (this.modal.offsetX * 2)) / 2
      const offsetY = (windowCenterY - (this.modal.offsetY * 2)) / 2

      // Used for reset rotate on modal show
      const card = this.$refs.card

      if (this.item.open && projectId !== this.id) {
        // Card rotate off - Reset card rotate
        this.cardRotate = false
        card.style.transform = `perspective(500px)`
        card.style.zIndex = `1002`
        this.modalStyle = {
          transform: `translate(${offsetX}px, ${offsetY}px)`,
          minWidth: `${minWidth}`,
          zIndex: '1002'
        }
        // Used for vue.Router
        if (projectId !== this.id) {
          // this.$router.push({ name: 'projects', params: {id: projectId} })
        }
      } else {
        this.modalStyle = {
          transform: 'translate(0px, 0px)',
          zIndex: '1002'
        }
        card.style.zIndex = `0`
        card.style.width = `100%`
        document.body.classList.remove('overlay-layer')
        document.body.style.overflow = 'auto'
        this.cardRotate = true

        // USED FOR vue.Router
        // let scrollY = document.body.style.top
        // document.body.style.position = ''
        // document.body.style.top = ''
        // window.scrollTo(0, parseInt(scrollY) * -1)
        // this.$router.push({ name: 'projectslist' })
      }
    },
    move (e) {
      if (this.cardRotate === true) {
        const card = this.$refs.card
        const relX = (e.offsetX + 1) / card.offsetWidth
        const relY = (e.offsetY + 1) / card.offsetHeight
        const rotY = `rotateY(${(relX - 0.5) * 5}deg)` // 5 = angle
        const rotX = `rotateX(${(relY - 0.5) * -5}deg)`
        card.style.transform = `perspective(500px) ${rotY} ${rotX}`
        card.style.transition = `transform 100ms ease`
      }
    },
    leave () {
      const card = this.$refs.card
      card.style.transform = `perspective(500px)`
      card.style.transition = `transform 350ms ease-in`
    }
  },
  mounted () {}
}
</script>

<style scoped>
.project-card {
  position: relative;
  z-index: 0;
  width: 100%;
  height: 350px;
  display: block;
  border-radius: 28px;
  /*border: 1px solid rgba(255, 255, 255, 0.3);*/
}
/*
.project-card.show {
  position: relative;
  z-index: 999;
  width: 100vw;
  height: 100vh;
  margin: 0 auto;
  padding-top: 64px;
  padding-bottom: 64px;
  overflow-y: auto;
}
*/
.modal {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 350px;
  display: block;
  z-index: 0 !important;
  border-radius: 28px;
  transition: all 0.5s cubic-bezier(.65,.14,.46,.9);
}
.modal .close {
  /*transform: rotate(45deg);*/
  position: absolute;
  right: 30px !important;
  top: 30px !important;
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(193, 193, 193, .9);
  border-radius: 50%;
  font-size: 1.8rem;
  padding: 0;
  display: block;
  pointer-events: none;
}
.modal-img {
  position: relative;
  z-index: 1;
  top: 0;
  left: 0;
  border-radius: 28px 28px 0 0;
  width: 100%;
  height: 350px;
}
.modal-dialog {
  width: 100%;
  max-width: 800px;
  margin: 0;
  padding: 0;
  overflow: auto;
}
.modal.show {
  width: 100%;
  height: 100vh;
  margin: 0 auto;
  padding-top: 64px;
  padding-bottom: 64px;
  overflow-y: auto;
}
.modal-content {
  background-color: #FEFEFE;
  border-radius: 28px 28px 0 0;
}
.modal-header {
  padding: 0;
  overflow: hidden;
}
.modal .close {
  display: none;
}
.modal.show .close {
  position: absolute;
  right: 30px !important;
  top: 30px !important;
  width: 2.5rem;
  height: 2.5rem;
  background-color: rgba(193, 193, 193, .9);
  border-radius: 50%;
  font-size: 1.8rem;
  padding: 0;
  display: block;
}
.modal-footer {
  padding: 0;
  border: none;
}
.modal-footer > * {
  margin: 0;
}

@media (max-width: 479px) {
  .project-card {
    height: 150px;
  }
  .modal {
    height: 150px;
  }
}
</style>



